export const addMultiValueQuery = query => (key, value) => (Object.assign({}, query, { [key]: query[key] ? [...query[key], value] : [value] }));
export const addSingleValueQuery = query => (key, value) => (Object.assign({}, query, { [key]: value }));
export const removeMultiValueQuery = query => (key, value) => {
    const valueIx = query[key] !== undefined ? query[key].indexOf(value) : -1;
    if (valueIx === -1) {
        return query;
    }
    return Object.assign({}, query, { [key]: [...query[key].slice(0, valueIx), ...query[key].slice(valueIx + 1)] });
};
export const removeSingleValueQuery = query => (key, value) => (Object.assign({}, query, { [key]: null }));
export const flatQueryValue = (name, value) => {
    if (typeof value === 'string') {
        return [{ name, value }];
    }
    return value.map(vl => ({ name, value: vl }));
};
